import { Component, OnInit } from '@angular/core';
import {Login} from '../login';
import {HttpClient} from '@angular/common/http';
import{Signup} from '../signup';
import {AppRoutingModule} from '../app-rounting.module';
import { Router } from '@angular/router';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
selectedLogin :Login;
flag:string;
success :boolean;
firstname :string;
login:Login={
  username:"",
  password:""
};
signup:Signup={
  username:"",
  firstname:"",
  lastname:"",
  password:""
};

constructor(private httpClient:HttpClient,private router:Router) {
}

  ngOnInit() {
  }
  onLogin():void{

this.httpClient.post('http://localhost:3000/login',{"username":this.login.username,"password":this.login.password}).subscribe ((data)=>{console.log(data,'here mr zobaidy');
let data2:any = data;
this.success = data2.logedin;
this.firstname = data2.firstname;
console.log(this.success);
console.log(this.firstname);
this.login.username= "";
this.login.password= "";
if(this.success.valueOf()==true){
  this.flag = "login sucessfull"+"  hello Mr "+this.firstname;
}
else{
this.flag = "login unsucessfull"+" Please try again ";
}
}
);

  }

  onSignup():void{
this.httpClient.post('http://localhost:3000/signup',{"username":this.signup.username,"firstname":this.signup.firstname,"lastname":this.signup.lastname,"password":this.signup.password}).subscribe((data)=>{console.log(data);
});
this.signup.firstname="";
this.signup.password="";
this.signup.lastname="";
this.signup.username="";
  }
}



