import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { LoginsucessComponent }   from './loginsucess/loginsucess.component';
import { LoginfailedComponent }   from './loginfailed/loginfailed.component';
const routes: Routes = [
  { path: 'sucess', component:LoginsucessComponent },
    { path: 'failed', component:LoginfailedComponent },

];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}



